@import "scss/fonts";

.App {
  text-align: center;

  padding: 0 0.5rem 0.5rem 0.5rem;

  @media screen and (min-width: 768px) {
    padding: 0 2rem 2rem 2rem;
  }
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  margin: 2rem 0;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: #1e359c;
}

.table-container {
  width: 100%;
  max-width: 80rem;
  box-sizing: border-box;
  border: 1px solid red;
  padding: 4px;
  margin: auto;
}

table {
  color: white;
  width: 100%;
  text-align: left;
  border-collapse: collapse;
  box-sizing: border-box;
  font-family: Gilroy, sans-serif;
  word-break: break-word;

  td:last-child {
    width: 70px;
  }
}

table, th, td {
  border: 1px solid white;
  padding: 4px;
}

input {
  margin-bottom: 2rem;
  width: 80vmin;
  padding: 0.5rem;
}

